  /* Carousel */
  #categories-list {
    font-family: 'Lato', sans-serif;
  }
  #categories-list .carousel-wrapper {
    background: grey;
    color: white;
    font-weight: 300;
    padding: 15px 0;
    text-transform: uppercase;
  }
  #categories-list .carousel-wrapper h2 {
    padding: 0 0 35px;
    text-align: center;
  }
  #categories-list .carousel-wrapper ol {
    margin: 0 auto;
    position: static;
    width: 100%;
  }
  #categories-list .carousel-wrapper ol span {
    border: 1px solid transparent;
    cursor: pointer;
    height: auto;
    padding: 10px 15px;
    text-align: center;
    -webkit-transition: border 0.3s, color 0.3s;
    transition: border 0.3s, color 0.3s;
    width: auto;
  }
  #categories-list .carousel-wrapper ol .active {
    background: none;
    border: 1px solid orange;
    color: orange;
    height: auto;
    padding: 10px 15px;
    width: auto;
  }
  #categories-list .carousel-inner .item {
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
  }
  #categories-list .carousel-inner .item .img-wrapper {
    height: 400px;
    margin: 0 -15px 0 0;
    overflow: hidden;
    position: relative;
  }
  #categories-list .carousel-inner .item .img-wrapper img {
    left: 50%;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 1000px;
  }
  #categories-list .carousel-inner .item .carousel-caption {
    background: orange;
    color: #111;
    font-size: 1.1em;
    height: 400px;
    margin: 0 0 0 -15px;
    padding: 30px;
    position: static;
    text-align: left;
    text-shadow: none;
  }
  #categories-list .carousel-inner .item .carousel-caption h2 {
    padding: 0 0 20px;
    text-transform: uppercase;
  }
  @media (max-width: 767px) {
    #categories-list .carousel-inner .item .img-wrapper {
      height: auto;
    }
    #categories-list .carousel-inner .item .img-wrapper img {
      -webkit-transform: none;
              transform: none;
      position: static;
      width: 100%;
    }
    #categories-list .carousel-inner .item .carousel-caption {
      height: auto;
    }
  }

* {
  box-sizing: border-box;
  font-family: "Open Sans";
}

h1, h2, p {
  margin: 0 auto 5px auto;
  padding: 0;
}

.wrapper {
  text-align: center;
}
.wrapper #container {
  margin: 0 auto;
  width: 100%;
  max-width: 1024px;
  text-align: left;
}
.wrapper #container .input-wrapper {
  position: relative;
  display: inline-block;
  margin-bottom: 1em;
  margin-top: 10px;
  width: calc(100% - 20.275em);
}
.wrapper #container .input-wrapper input {
  width: 100%;
  padding: 0.5em;
  padding-left: 2em;
  border: 2px solid rgba(198, 198, 198, 0.9);
  font-size: 1em;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 4px;
}
.wrapper #container .input-wrapper i {
  position: absolute;
  top: 50%;
  left: 0.75em;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: rgba(198, 198, 198, 0.9);
}
.wrapper #container button {
  position: relative;
  display: inline-block;
  margin-left: 0.5em;
  padding: 0.6em 1em;
  background: #fefefe;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  overflow: hidden;
  z-index: 1;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  font-size: 15px;
  font-weight: bold;
  border: 2px solid rgba(198, 198, 198, 0.9);
  border-radius: 4px;
  color: rgba(160, 160, 160, 0.9);
}
.wrapper #container button:hover {
  color: white;
}
.wrapper #container button:hover:after {
  background: rgba(198, 198, 198, 0.9);
  -webkit-transform: translate(0%);
          transform: translate(0%);
}
.wrapper #container button:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  z-index: -1;
}
.wrapper #container .question {
  margin: 0.75em auto;
  padding: 0.5em;
  background: rgba(198, 198, 198, 0.1);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.25);
  -webkit-transition: all 0.1s, border 0.25s ease-out;
  transition: all 0.1s, border 0.25s ease-out;
}
.wrapper #container .question[data-color="gray"] {
  border-left: 4px solid rgba(198, 198, 198, 0.9);
}
.wrapper #container .question[data-color="yellow"] {
  border-left: 4px solid #ffba00;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
.wrapper #container .question[data-color="green"] {
  border-left: 4px solid #7ad03a;
  box-shadow: 0 6px 6px 0 rgba(0, 0, 0, 0.25);
}
.wrapper #container .question[data-color="red"] {
  border-left: 4px solid #ea0000;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.15);
}
.wrapper #container .question .votes {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0.5em 0 0;
}
.wrapper #container .question .votes .upvote {
  position: relative;
  padding: 1em;
  cursor: pointer;
}
.wrapper #container .question .votes .upvote:after {
  content: "";
  width: 0;
  height: 0;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid rgba(0, 0, 0, 0.75);
}
.wrapper #container .question .votes .number-of-votes {
  margin: 0 auto;
  text-align: center;
  font-size: 1.25em;
}
.wrapper #container .question .votes .downvote {
  position: relative;
  padding: 1em;
  cursor: pointer;
}
.wrapper #container .question .votes .downvote:after {
  content: "";
  width: 0;
  height: 0;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-top: 7px solid rgba(0, 0, 0, 0.75);
}
.wrapper #container .question .question-and-answer {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 5em);
}

.dropbtn {
