    top: 49px;
  }

  #post .hide-comments .button {
    z-index: 1;
    margin-bottom: 50px;
    position: relative;
  }

  #post .hide-comments #hide-button {
    display: none;
  }

  #post .hide-comments #hide-button + label {
    width: 200px;
  }


  #post .hide-comments #new-comment {
    display: none;
  }

  #post .hide-comments #new-comment + label {
    width: 200px;
  }

  #post .comments {
    margin: 5px 30px 0 110px;
  }

  #post .comments .user-comment {
    position: relative;
  }

  #post .comments .user-comment img {
    vertical-align: top;
    width: 32px;
    height: 32px;
    background-color: #7bb0ff;
    border-radius: 50%;
    margin-right: 20px;
    float: left;
  }

  #post .comments .user-comment header {
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    max-width: 75%;
    line-height: 16px;
  }

  #post .comments .user-comment header span {
    color: #b4b9c8;
    font-size: 12px;
    font-weight: 500;
    margin-left: 25px;
    line-height: 10px;
  }

  #post .comments .user-comment .content {
    margin: 5px 0 0 50px;
    height: auto;
    display: block;
    overflow: auto;
  }

  #post .comments .user-comment .content img {
    display: block;
    width: 100%;
    border-radius: 0;
    height: auto;
  }

  #post .comments .user-comment .delete {
    position: absolute;
    color: #b4b9c8;
    font-size: 12px;
    font-weight: 500;
    top: 25px;
    right: 0;
  }

  #post .comments .user-comment .delete:hover {
    color: #79829d;
  }



  #post .comments .user-comment .edit {
    position: absolute;
    color: #b4b9c8;
    font-size: 12px;
    font-weight: 500;
    top: 3px;
    right: 0;
  }

  #post .comments .user-comment .edit:hover {
    color: #79829d;
  }

  #post .comments ul {
    display: block;
    list-style: none;
  }

  #post .comments ul li {
    display: block;
    padding-bottom: 40px;
  }

  #post .comments ul li > ul {
    padding-left: 50px;
    padding-top: 40px;
  }

  #post .comments ul li > ul li {
    padding-bottom: 0;
  }

  #dropdown {
    text-align: center;
    margin: 30px 0;
  }

  #dropdown img {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    background-color: #3a3e47;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
  }

  #dropdown ul {
    list-style: none;
    overflow: visible;
    position: relative;
    -webkit-transition: all .8s;
    transition: all .8s;
  }

  #dropdown ul li:not(:last-child) {
    border-bottom: 1px solid #e4e9f5;
  }

  #dropdown ul li:first-child {
    border-radius: 8px 8px 0 0;
  }

  #dropdown ul li:last-child {
    border-radius: 0 0 8px 8px;
  }

  #dropdown ul li {
    height: 75px;
    line-height: 75px;
  }

  #dropdown ul li:hover {
    background-color: #eef1f1;
  }

  #dropdown ul:before {
    content: '';
    display: block;
    position: absolute;
    top: -15px;
    left: calc(50% - 20px);
    border-bottom: 15px solid #ffeeff;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    /*-webkit-clip-path: polygon(50% 50%, 0% 100%, 100% 100%);
              clip-path: polygon(50% 50%, 0% 100%, 100% 100%);*/
  }

  #dropdown a {
    color: #727988;
    font-size: 18px;
    font-weight: 400;
  }

  #dropdown a:hover {
    color: #4f545e;
  }

